.shorts-page {
    block-size: 100%;
}
.shorts-page .short {
    inline-size: 100%;
    block-size: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    position: relative;
}
.shorts-page .short .player {
    block-size: 43rem;
    inline-size: 24.2rem;
    position: relative;
}
.shorts-page .short .player iframe {
    inline-size: 100%;
    block-size: 100%;
}
.shorts-page .short .info {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.shorts-page .short .info>* {
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    color: var(--secondary-2);
    justify-content: center;
    align-items: center;
    font-size: .9rem;
}
.shorts-page .short .info>* .icon {
    padding: 1rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--primary-3);
    transition: .3s all ease-in-out;
    cursor: pointer;
}
.shorts-page .short .info>* .icon img{
    opacity: .7;
    transition: .3s all ease-in-out;
}
.shorts-page .short .info>* .icon:hover {
    background-color: var(--primary-7);
    scale: 1.1;
}
.shorts-page .short .info>* .icon:hover img {
    opacity: 1;
}
@media (max-width: 768px) {
    .shorts-page .short {
        inline-size: 50vw;
        margin: auto;
    }
    .shorts-page .short .player {
        inline-size: 100%;
    }
    .shorts-page .short .info {
        position: absolute;
        inset-inline-end: .5rem;
    }
    .shorts-page .short .info>* .icon {
        padding: .5rem;
        opacity: .7;
    }
    .shorts-page .short .info>* .icon img {
        block-size: 1.5rem;
    }
    .shorts-page .short .player {
        block-size: 118vw;
        min-block-size: 70svh;
    }
}
@media (max-width: 600px) {
    .shorts-page .short {
        inline-size: 67vw;
    }
}
@media (max-width: 500px) {
    .shorts-page .short {
        inline-size: 84vw;
    }
}